:root {
  --light1:#25053E;
  --light2:#442D56;
  --dark1:#FDE0D6;
  --dark2:#FDEAE3;
  --highlight1:#FED44E;
  --highlight2:#FDC104;
  --main-font: 'Lexend Mega', sans-serif;
  --secondary-font: 'Nunito Sans' sans-serif;
}

body {
  width: 100%;
  font-family: var(--secondary-font);
}

.page {
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.huge-center-title {
  color: var(--dark2);
  font-size: 45px;
  line-height: 50px;
  font-style: normal;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin: 100px 0 30px 0;
}

#our-team .huge-center-title {
  margin-top: 50px;
  padding-top: 50px;
}

.huge-center-subtitle {
  color: var(--dark1);
  font-size: 15px;
  line-height: 22px;
  font-style: normal;
  margin-bottom: 30px;
}

.inline-block{
  display: inline-block;
}

.margin-bottom {
  margin-bottom: 150px;
}
.margin-left-30 {
  margin-left: 30px;
}
.margin-bottom-10 {
  margin-bottom: 10px;
}
.margin-bottom-20 {
  margin-bottom: 20px;
}
.margin-bottom-30 {
  margin-bottom: 30px;
}
.margin-bottom-50 {
  margin-bottom: 50px;
}
.margin-top-50 {
  margin-top: 50px;
}
.margin-top-80 {
  margin-top: 80px;
}

.text-right {
  text-align: right;
}

.color1 {
  background-color: magenta;
  color: var(--light1);
}
.color2 {
  background-color: yellow;
  color: var(--dark2);
}
.color3 {
  background-color: green;
  color: var(--dark2);
}
.color4 {
  background-color: blue;
  color: var(--light1);
}
.color5 {
  background-color: #BC2622;
  color: var(--light1);
}

.color6 {
  background: url('bg1.jpg');
  background-size:cover;
  color: var(--light1);
}

.fade-in {
  animation: fadeIn ease 1s;
  -webkit-animation: fadeIn ease 1s;
  -moz-animation: fadeIn ease 1s;
  -o-animation: fadeIn ease 1s;
  -ms-animation: fadeIn ease 1s;
}

@media (min-width: 1200px) {
  .container, .container-lg, .container-md, .container-sm, .container-xl {
    max-width: 1300px !important;
  }
}

@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-moz-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-o-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-ms-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}
