#first-page {
    background: url('header.png') no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

#home {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
}

#content {
    padding: 3rem;
    background-color: white;
}

#title {
    font-family: var(--main-font);
    font-size: 4rem;
}

#subtitle {
    font-size: 2rem;
    text-align: center;
}

#register-button {
    display: flex;
    justify-content: center;
    padding-top: 20px;
    margin: auto;
}

#register{
    background-color: var(--light1);
    color: var(--dark2);
    font-size: 1rem;
    padding: 8px 30px;
}

@media (max-width: 768px) {
    #content {
        width: auto;
        height: auto;
        position: relative;
        left: 0;
        top: 0;
        transform: none;
        font-size: 40px;
        margin-bottom: 0;
        padding: 80px 0 50px;
    }

    #first-page {
        height: auto;
    }
    #subtitle {
        margin-bottom: 20px;
    }
    #menu {
        display: none;
    }
}
