#industrial-partners {
    color: var(--highlight2);
    background-color: var(--light1);
    padding: 140px 0;
    letter-spacing: 2px;
}

#industrial-partners-title {
    font-family: 'Rubik', sans-serif;
    font-size: 45px;
    line-height: 50px;
    font-style: normal;
    font-weight: 700;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin-bottom: 70px;
}
#industrial-partners-list {
    font-family: 'Rubik', sans-serif;
    font-size: 20px;
    line-height: 25px;
    font-style: normal;
    font-weight: bold;
    /* letter-spacing: 2px; */
    /* text-transform: uppercase; */
    margin-bottom: 35px;

}
#industrial-partners-touch {
    margin-bottom: 30px;
}
#industrial-partners-touch-for-projects {
    margin-bottom: 30px;
    margin-top: 30px;
}

#industrial-partners-touch-button {
    background-color: var(--dark1);
    font-family: "Roboto", Sans-serif;
    font-weight: 500;
    font-size: 15px;
    padding: 12px 24px;
    -webkit-border-radius: 3px;
    border-radius: 3px;
    color: var(--light1);
    cursor: pointer;
    letter-spacing: 0px;
}
#industrial-partners-touch-button  a, #industrial-partners-touch-button  a:hover ,#industrial-partners-touch-button  a:active{
    color: var(--light1);
    text-decoration: none;
}
#industrial-partners-touch-button-for-projects   a, #industrial-partners-touch-button-for-projects   a:hover ,#industrial-partners-touch-button-for-projects a:active{
    color: var(--light1);
    text-decoration: none;
}
#industrial-partners-touch-button-for-projects {
    background-color: var(--dark1);
    font-family: "Roboto", Sans-serif;
    font-weight: 500;
    font-size: 15px;
    padding: 12px 24px;
    -webkit-border-radius: 3px;
    border-radius: 3px;
    color: var(--light2);
    cursor: pointer;
    letter-spacing: 0;
}

.sponsorList {
    display: flex;
    justify-content: space-around;
}

.sponsor {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sponsorImage {
    height: auto;
    width: 70px;
}
