#projects {
    background-color: var(--light1);
}

.project-card{
    box-shadow: 0 0 10px 0 rgb(0 0 0 / 15%);
    transition: all .25s;
    border-radius: 3px;
    -webkit-box-shadow: 0 0 10px 0 rgb(0 0 0 / 15%);
    background-color: var(--light1);
    border: 0 solid var(--light2);
    padding: 20px 30px;
    margin-bottom: 35px;
}
.project-card:hover {
    -webkit-box-shadow: 0 0 30px 0 rgb(0 0 0 / 15%);
    box-shadow: 0 0 30px 0 rgb(0 0 0 / 15%);
}
.card-title-self {
    font-size: 25px;
    line-height: 30px;
    text-transform: uppercase;
    color: var(--dark1);
    font-weight: 600;
    letter-spacing: 1px;
    margin-bottom: 25px;
    margin-top: 20px;
    min-height: 60px;
}

.card-content {
    color: var(--light1);
    font-family: 'RobotoLight';
    letter-spacing: 1px;
    font-size: 14px;
    line-height: 1.7;
    margin-bottom: 25px;

    height: 100px;
    text-overflow: ellipsis;
}

.card-read-more {
    font-family: 'Roboto';
    margin-bottom: 35px;
    color: var(--dark1);
    font-weight: 700;
    font-size: 12px;
}

.prof-name {
    margin-bottom: 20px;
    font-weight: 500;
}

.project-page {
    color: var(--dark2);
    background-color: var(--light1);
    font-size: 20px;
    font-weight: normal;

    /* letter-spacing: 2px; */
    /* margin-top: 100px; */
}
.project-page-col {
    padding: 60px 60px !important;
    /* min-height: 100vh; */
}
.project-budget {
    /* text-align: right; */
}
.project-sponsor {
    /* text-align: left; */
}

.project-team {
    margin-top: 80px;
}


.project-page-title {
    font-family: 'Rubik', sans-serif;
    font-size: 45px;
    line-height: 50px;
    font-style: normal;
    font-weight: 700;
    letter-spacing: 1px;
    /* text-transform: uppercase; */
    margin-bottom: 90px;
}
.project-page-list {
    font-family: 'Rubik', sans-serif;
    font-size: 20px;
    line-height: 25px;
    font-style: normal;
    font-weight: bold;
    letter-spacing: 2px;
    text-transform: uppercase;
    margin-bottom: 35px;

}
.project-page-touch {
    margin-bottom: 30px;
}
.project-page-touch-button {
    background-color: var(--dark1);
    font-family: "Roboto", Sans-serif;
    font-weight: 500;
    font-size: 15px;
    padding: 12px 24px;
    -webkit-border-radius: 3px;
    border-radius: 3px;
    color: var(--light2);
    cursor: pointer;
    letter-spacing: 0;
}

#specific-project-page {
    min-height: 120vh;
    padding-bottom: 20vh;
}

.left-project-section {
    margin-bottom: 60px;
    font-family: 'Rubik', sans-serif;
    font-size: 20px;
    line-height: 40px;
    font-style: normal;
}

.left-project-section b {

    font-weight: 700;
}
#project-paragraphs {
    list-style-type: none;
}
#project-paragraphs li {
    margin-bottom: 65px;
    font-family: 'RobotoThin';
    font-size: 25px;
    line-height: 40px;
}
#project-paragraphs li::before {
    display: block;
    position: absolute;
    left: -10px;
    transform: translateY(10px);
    vertical-align: middle;
    width: 20px;
    height: 20px;
    background-color: var(--dark2);
    margin-right: 8px;
    content: ' ';
}
@media (max-width: 768px) {
    #project-paragraphs li::before {
        left: 15px;
    }
    .left-project-section {
        display: block;
        text-align: center;
    }
}
#more-projects-link {
    display: inline-block;
    border: 1px solid var(--dark1);
    padding: 8px 50px;
    background-color: var(--dark1);
}
#more-projects-a, #more-projects-a:hover, #more-projects-a:active {
    color: var(--light1);
}
